import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import home from '@/views/home'//我的
import list from '@/views/list'//列表
import discover from '@/views/discover'//发现
import my from '@/views/my'//我的
import temple from '@/views/temple'//我的
import test2 from '@/views/vue2'
import test3 from '@/views/vue3'

Vue.use(Router)
/**
 * 路由守卫 登录检测 islogin
 *
 * 需要登录的页面路由需要添加 
 * meta.requiresAuth = true
 *
 * 登录后不可访问的路由需要添加
 * meta.canEnterOrNot = true
 * 
 */
const islogin=false;
const router = new Router({
	//base: '/个人项目/personal_project/vue/webpack_vue/demo6',
	mode:'history',
	//mode:'hash',
  routes: [
    {
      path: '/',
      redirect:'/home'//重定向
    },
    {
      	path: '/home',
      	name: 'home',
      	component: home,
      	beforeEnter(routeTo, routeFrom, next) {
	      //next({ name: 'test2' })
	      next();
	    },
    },
    {
      	path: '/list',
      	name: 'list',
      	component: list,
      	
    },
    {
      	path: '/discover',
      	name: 'discover',
      	component: discover,
      	
    },
    {
      	path: '/my',
      	name: 'my',
      	component: my,
      	
    },
    {
      	path: '/temple',
      	name: 'temple',
      	component: temple,
      	
    },
    {
      	path: '/test2',
      	name: 'test2',
      	component: test2,
      	
    },
    {
      	path: '/test3',
      	name: 'test3',
      	component: test3,
      	
    }
  ]
});

export default router;



