<template>
	<div id="pageDiv" :style="{'padding-top':isWeiXin || isApps?'0':''}">
		<my-heade :para_head="para_head"></my-heade>
		
		<!-- 页面的主要内容 -->
        <section class="content">
			
        </section>
        
        
        <footerNav></footerNav>
	</div>
</template>

<script>
	import myHeade from '../components/myHeade';
	import footerNav from '../components/footer_nav';
	export default{
		name : 'home',
		components:{
           myHeade,
           footerNav
        },
        data : ()=>({
            isWeiXin:false,
            isApps:false,
            para_head:'no_btn',//传递给头部参数
        }),
		created() {
		  
		}
	}
</script>

<style lang="less" scoped>
    // 主要页面布局
    #pageDiv{
        background:#fff;
        position: relative;
        box-sizing: border-box;
        overflow: hidden;
        height: 100%;
        padding-top: 46px;
        padding-bottom: 48px;
    }
    
    .content{
        height: 100%;
        padding: 8px;
        background: #f2f2f2;
        overflow: scroll;
        padding-bottom:0;
    }
    
</style>


